<template>
    <div>
      <iframe :src="src" frameborder="0" ></iframe>
    </div>
</template>

<script>
    export default {
        name: "previewPdf",
        mounted(){
          this.src = this.$route.query['src']
        },
        data(){
          return{
            src:''
          }
        }
    }

</script>

<style scoped>
  div{
    margin: 0;
    padding: 0;
    height: 100%;
    width: 100%;
  }

  iframe{
    width: 100%;
    height: 100%;
  }


</style>



